    sendToken (amount, decimals = 18) {
      const jsonInterface = [
        {
          constant: false,
          inputs: [
            {name: '_to', type: 'address'},
            {name: '_amount', type: 'uint256'}
          ],
          name: 'transfer',
          outputs: [{name: '', type: 'bool'}],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        }
      ];
      const contract = new this.web3.eth.Contract(jsonInterface);
      const data = contract.methods
          .transfer(
              this.tokenAddress.toLowerCase(),
              new BigNumber(amount).times(new BigNumber(10).pow(decimals)).toFixed()
          )
          .encodeABI();

      let gasLimit = 100000;
      this.web3.eth
          .estimateGas({
            from: this.userAddress,
            to: this.tokenAddress,
            value: 0,
            data
          })
          .then(gas => {
            console.log(gas);
            gasLimit = gas;
          })
          .catch(console.error);

      this.web3.eth.getGasPrice().then(gasPrice => {
        this.web3.eth.getTransactionCount(this.userAddress).then(nonce => {
          this.web3.eth
              .sendTransaction({
                from: this.userAddress,
                to: this.tokenAddress,
                nonce,
                value: 0,
                gasPrice: gasPrice,
                gas: gasLimit,
                data
              })
              .once('transactionHash', hash => {
                console.log(['Hash', hash]);
                this.txHash = hash;
              })
              .once('receipt', res => {
                console.log(['Receipt', res]);
              })
              .on('error', err => {
                console.log(['Error', err]);
              })
              .then(txhash => console.log('THEN: ', txhash));
        });
      });
    },
